<template>
	<view style="background-color: #ffffff;height: 100vh;">
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">话题</block>
		</cu-custom>
		<!-- <swiper class="screen-swiper" :class="dotStyle?'square-dot':'round-dot'" :indicator-dots="true" :circular="true"
			:autoplay="true" interval="5000" duration="500">
			<swiper-item v-for="(item,index) in swiperList" :key="index">
				<image :src="item.url" mode="aspectFill" v-if="item.type=='image'"></image>
				<video :src="item.url" autoplay loop muted :show-play-btn="false" :controls="false" objectFit="cover"
					v-if="item.type=='video'"></video>
			</swiper-item>
		</swiper> -->

		<!-- <view style="width: 100%;display: flex;justify-content: center;">
			<img :src="trans_pic" width="100%" height="400rpx"> -->
		<!-- 			<image :src="trans_pic" width="100%" height="400rpx"></image> -->
		<!-- </view> -->


		<view class="text-content">
			<view class="bigtitle">
				{{title}}
			</view>
			<view class="content-detail">
				{{detail}}
			</view>
		</view>





		<modal v-if="areaShow" title="回复评论" confirm-text="保存" cancel-text="取消" @cancel="cancelAdd"
			@confirm="confirmAdd">
			<input type="text" v-model="formvisi.content" :placeholder="placeholdercontent" class="intxt"
				maxlength="100" />
		</modal>

		<!-- 
		        @param: commentList展示的评论列表数据
		        @method: clickPraise 点赞评论
		        @method: clickDelete 删除父级评论
		        @method: clickRecommentChild 点赞子评论
		        @method: clickDeleteChild 删除子评论
		         -->
		<five-mul-commentlist :commentList="commentList" @clickPraise="clickPraiseComment"
			@clickDelete="clickDeleteComment" @clickDeleteChild="clickDeleteCommentChild"
			@clickRecomment="clickRecomment" @clickRecommentChild="clickRecommentChild"></five-mul-commentlist>
		<view class="cu-bar foot input" :style="[{bottom:InputBottom+'px'}]">
			<!-- <view class="action">
				<text class="cuIcon-sound text-grey"></text>
			</view> -->
			<input class="solid-bottom" :adjust-position="false" :focus="false" maxlength="300" cursor-spacing="10"
				@focus="InputFocus" @blur="InputBlur" v-model="textarea"></input>

			<button class="cu-btn bg-green shadow" @tap="submitfun">发送</button>
		</view>
	</view>
</template>

<script>
	export default {
		onLoad(options) {
			console.log(options)
			this.title = options.title
			this.detail = options.detail
			this.id = options.id
		},
		mounted() {
			try {
				this.userinfo = uni.getStorageSync('userinfo')
			} catch (e) {
				this.userinfo = ""
			}
		},
		onShow() {
			this.querychatfun()
		},
		data() {
			return {
				textarea: '',
				formvisi: {
					content: ''
				},
				areaShow: false,
				areaTxt: '',
				InputBottom: 0,
				userinfo: {},
				title: '',
				detail: '',
				trans_pic: '',
				id: '',
				recevobj: '',
				commentList: [],
				placeholdercontent: '',
				TabCur: 0,
				scrollLeft: 0,
				cardCur: 0,
				form: {},
				swiperList: [],
				dotStyle: false,
				towerStart: 0,
			}
		},
		methods: {
			cancelAdd() {
				this.areaShow = false
			},
			confirmAdd() {

				this.submitsubfun()
				this.areaShow = false
			},

			InputFocus(e) {
				this.InputBottom = e.detail.height
			},
			InputBlur(e) {
				this.InputBottom = 0
			},
			async querychatfun() {
				let that = this
				let objext = {
					id: this.id,
				}
				let resdata = await new this.Request(this.Urls.m().querychat, objext).modepost()
				let res = resdata.map((v, i) => {
					v.CANDELETE = v.candelete
					v.COMMENT = v.comment
					v.COMMENT_TIME = v.comment_time
					v.FIRSTNICKNAME = v.firstnickname
					v.HEADIMGURL = v.headimgurl
					v.IS_PRAISE = v.is_praise
					v.PARENTID = v.parentid
					v.PRAISE_NUM = v.praise_num
					v.SECONDNICKNAME = v.secondnickname
					return v
				})
				let parentarr = []
				res.forEach(element => {
					if (element.PARENTID == 0) {
						parentarr.push(element)
					}
				});
				parentarr = parentarr.map((val, i) => {
					val.CHILD_ANWSER_LIST = []
					return val
				})
				for (let i = 0; i < parentarr.length; i++) {
					for (let j = 0; j < res.length; j++) {
						if (parentarr[i].id == res[j].PARENTID) {
							parentarr[i].CHILD_ANWSER_LIST.push(res[j])
						}
					}
				}
				that.commentList = parentarr




			},
			tologin() {
				uni.showToast({
					title: '请先登录',
					icon: 'none',
					duration: 2000
				});
				setTimeout(() => {
					uni.navigateTo({
						url: '/pages/login/login'
					});
				}, 2000)

			},

			//发表新评论
			async submitfun() {
				let that = this
				console.log(this.userinfo)
				if (!this.userinfo) {
					this.tologin()
					return false
				}
				let obj = {
					annid: this.id,
					firstnickname: this.userinfo.userName,
					is_praise: null,
					comment: this.textarea,
					praise_num: 0,
					candelete: 0,
					headimgurl: "https://ossweb-img.qq.com/images/lol/web201310/skin/big10006.jpg",
					parentid: 0,
					secondnickname: null,
					comment_time:this.getnowdate() + " " + this.getnowtime()
				}

				let resdata = await new this.Request(this.Urls.m().insertchat, obj).modepost()
				if (resdata == "success") {
					uni.showToast({
						title: '提交成功',
						icon: 'success',
						duration: 2000
					});
					that.textarea = ""
					that.querychatfun()

				} else {
					uni.showToast({
						title: '操作失败',
						icon: 'none',
						duration: 2000
					});
					return false
				}

				

			},
			         getnowdate(){
			        var nowDate = new Date();
			        var year = nowDate.getFullYear();
			        var month = nowDate.getMonth() + 1 < 10 ? "0" + (nowDate.getMonth() + 1): nowDate.getMonth() + 1;
			        var day = nowDate.getDate() < 10 ? "0" + nowDate.getDate() : nowDate.getDate();
			        var dateStr = year + "-" + month + "-" + day;
			        return dateStr;
			    },
			
			 getnowtime(){
			          let date = new Date()
			          let hours = date.getHours()
			          let minutes = date.getMinutes() < 10 ? ('0' + date.getMinutes()) : date.getMinutes()
			          let seconds = date.getSeconds() < 10 ? ('0' + date.getSeconds()) : date.getSeconds()
			          let time =hours + ':' + minutes + ':' + seconds
			          return time
			        
			    },
			//回复评论
			async submitsubfun() {
				let that = this
				if (!this.userinfo) {
					this.tologin()
					return false
				}
				let parid
				if (this.recevobj.PARENTID == 0) {
					parid = this.recevobj.id
				} else {
					parid = this.recevobj.PARENTID
				}
				console.log(this.recevobj)
				let obj = {
					annid: this.id,
					firstnickname: this.userinfo.userName,
					is_praise: null,
					comment: this.formvisi.content,
					praise_num: 0,
					candelete: 0,
					headimgurl: "https://ossweb-img.qq.com/images/lol/web201310/skin/big10006.jpg",
					parentid: parid,
					secondnickname: this.recevobj.FIRSTNICKNAME,
					comment_time:this.getnowdate() + " " + this.getnowtime()
				}
				let resdata = await new this.Request(this.Urls.m().insertchat, obj).modepost()
				if (resdata == "success") {
					uni.showToast({
						title: '提交成功',
						icon: 'success',
						duration: 2000
					});
					that.formvisi.content = ""
					that.querychatfun()
				
				} else {
					uni.showToast({
						title: '操作失败',
						icon: 'none',
						duration: 2000
					});
					return false
				}

				


			},
			clickPraiseComment(val) {
				console.log("clickPraiseComment", val)
			},
			clickDeleteComment(val) {
				console.log("clickDeleteComment", val)
			},
			clickDeleteCommentChild(val) {
				console.log("clickDeleteCommentChild", val)
			},
			clickRecomment(val) {
				console.log(val)
				this.areaShow = true
				this.placeholdercontent = `回复 ${val.FIRSTNICKNAME}：`
				this.recevobj = val
			},
			clickRecommentChild(val) {
				console.log(val)
				this.areaShow = true
				this.placeholdercontent = `回复 ${val.FIRSTNICKNAME}：`
				this.recevobj = val
			},
			DotStyle(e) {
				this.dotStyle = e.detail.value
			},
			// cardSwiper
			cardSwiper(e) {
				this.cardCur = e.detail.current
			},
			// towerSwiper
			// 初始化towerSwiper
			TowerSwiper(name) {
				let list = this[name];
				for (let i = 0; i < list.length; i++) {
					list[i].zIndex = parseInt(list.length / 2) + 1 - Math.abs(i - parseInt(list.length / 2))
					list[i].mLeft = i - parseInt(list.length / 2)
				}
				this.swiperList = list
			},

			// towerSwiper触摸开始
			TowerStart(e) {
				this.towerStart = e.touches[0].pageX
			},

			// towerSwiper计算方向
			TowerMove(e) {
				this.direction = e.touches[0].pageX - this.towerStart > 0 ? 'right' : 'left'
			},

			// towerSwiper计算滚动
			TowerEnd(e) {
				let direction = this.direction;
				let list = this.swiperList;
				if (direction == 'right') {
					let mLeft = list[0].mLeft;
					let zIndex = list[0].zIndex;
					for (let i = 1; i < this.swiperList.length; i++) {
						this.swiperList[i - 1].mLeft = this.swiperList[i].mLeft
						this.swiperList[i - 1].zIndex = this.swiperList[i].zIndex
					}
					this.swiperList[list.length - 1].mLeft = mLeft;
					this.swiperList[list.length - 1].zIndex = zIndex;
				} else {
					let mLeft = list[list.length - 1].mLeft;
					let zIndex = list[list.length - 1].zIndex;
					for (let i = this.swiperList.length - 1; i > 0; i--) {
						this.swiperList[i].mLeft = this.swiperList[i - 1].mLeft
						this.swiperList[i].zIndex = this.swiperList[i - 1].zIndex
					}
					this.swiperList[0].mLeft = mLeft;
					this.swiperList[0].zIndex = zIndex;
				}
				this.direction = ""
				this.swiperList = this.swiperList
			},
		}
	}
</script>

<style>
	.tower-swiper .tower-item {
		transform: scale(calc(0.5 + var(--index) / 10));
		margin-left: calc(var(--left) * 100upx - 150upx);
		z-index: var(--index);
	}

	.titlecal {
		text-align: center;
		font-size: 2em;
		font-weight: bold;
	}

	.detailcal {
		margin-bottom: 20rpx;
		padding: 20rpx;
		border-bottom: 1px solid #f1f1f1;
	}

	.text-content {
		margin-top: 5%;
		padding-left: 3%;
		padding-right: 3%;
		height: 20%;
	}


	.content-detail {
		width: 100%;
		word-wrap: break-word;
		word-break: break-all;
	}

	.bigtitle {
		font-size: 1.2em;
		font-weight: bold;
	}
</style>